<template>
	<section class="admin">
		<div class="container-fluid">
			<div class="row">
				<div class="col-12 col-lg-3">
					<admin-sidebar />
				</div>
				<!-- /.col-12 col-lg-4 -->
				<div class="col-lg-9">
					<component :is='currentTab' />
 				</div>
				<!-- /.col-lg-8 -->
			</div>
			
		</div>
	</section>
</template>
<script>
	const AdminSidebar = () => import('@/components/admin/AdminSidebar.vue');

	const ManageBlog = () => import('@/components/admin/manage/ManageBlog.vue');
	const ManageWorks = () => import('@/components/admin/manage/ManageWorks.vue')
	const ManageDrafts = () => import('@/components/admin/manage/ManageDrafts.vue')
	const ManagePersonal = () => import('@/components/admin/manage/ManagePersonal.vue')
	const ManageServices = () => import('@/components/admin/manage/ManageServices.vue')

	export default {
		name: 'adminPage',
		data() {
			return {}
		},
		computed: {
			activeTab() {
				return this.$store.getters.ActiveTab;
			},
			currentTab() {
				switch(this.activeTab) {
					case 'personal':
						return 'manage-personal'
						break;
					case 'posts':
						return 'manage-blog';
						break;
					case 'drafts':
						return 'manage-drafts';
						break;
					case 'works':
						return 'manage-works';
						break;
					case 'services':
						return 'manage-services'
						break;
				}
			}
		},
		metaInfo() {
			return {
				title: 'Майстерня',
				titleTemplate: '%s | Юрій Колцьо',
				meta: [
					{
						name: 'description',
						content: 'Майстерня Юрія Колцьо'
					},
					{
						property: 'og:description',
						content: 'Майстерня Юрія Колцьо'
					}
				],
			}
		},
		components: {
			AdminSidebar,
			ManageBlog,
			ManageWorks,
			ManageDrafts,
			ManagePersonal,
			ManageServices
		}
	}
</script>